@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "";
  font-display: swap;
  src: url("../../fonts/") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Rouble";
  font-display: swap;
  src: url("../../fonts/rouble.woff2") format("woff2");
}
@font-face {
  font-family: "Bebas Neue";
  font-style: normal;
  font-weight: 400;
  src: local("Bebas Neue"), url("../../fonts/bebasneueregular.woff2") format("woff2");
}
@font-face {
  font-family: "Bebas Neue";
  font-style: normal;
  font-weight: 700;
  src: local("Bebas Neue"), url("../../fonts/bebasneuebold.woff2") format("woff2");
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: local("Roboto Condensed"), url("../../fonts/Robotocondensed.woff2") format("woff2");
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: local("Roboto Condensed"), url("../../fonts/Robotocondensedbold.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.section-heading {
  color: #102654;
  font-family: "Bebas Neue", "Monaco", monospace;
  font-size: 49px;
  font-weight: 700;
  line-height: 49px;
  letter-spacing: 0.03em;
  text-align: left;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background: transparent;
  border: none;
  font-size: 12px;
  font-weight: 700;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .btn:hover, .btn:focus {
    background: linear-gradient(148.82deg, #D10A42 0.85%, #00359F 100%), linear-gradient(0deg, #FFFFFF, #FFFFFF);
  }
}

.btn-white {
  padding: 18px;
  color: #ffffff;
  outline: 4px solid #ffffff;
}

.btn-blue {
  padding: 23px 27px;
  color: #102654;
  outline: 4px solid #102654;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.video {
  position: absolute;
  top: 54%;
  right: 34%;
  width: 364px;
  height: 228px;
  background-color: #000000;
}
.video:hover .video__button, .video:focus .video__button {
  opacity: 0.8;
}

.video__media {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

.video__button {
  position: absolute;
  display: none;
  top: 50%;
  left: 50%;
  width: 100px;
  height: 100px;
  border: none;
  padding: 0;
  transform: translate(-50%, -50%);
  outline: none;
  background: url(../../img/svg/icon-play.svg);
  cursor: pointer;
}
.video__button:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.video--enabled {
  cursor: pointer;
}

.video--enabled .video__button {
  display: block;
}

.header {
  color: #ffffff;
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, #B80027 -46.69deg, #003D85 32.24deg, #B80027 313.31deg, #003D85 392.24deg);
}

.header__wrapper {
  padding: 30px 103px 46px;
}

.header__contacts {
  list-style-type: none;
  font-family: "Bebas Neue", "Monaco", monospace;
  font-size: 26px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: 0.02em;
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
}

.header__contact a {
  color: #ffffff;
}
.header__contact span {
  letter-spacing: 1px;
}

.header__intro {
  display: grid;
  grid-template-columns: 397px 508px 128px;
  justify-content: space-between;
}
.header__intro button {
  grid-column: -1/1;
  grid-row: 3/4;
  justify-self: end;
}
.header__intro img {
  grid-column: 2/3;
  grid-row: 1/-1;
  position: relative;
  bottom: 5px;
}

.header__block {
  grid-column: 1/2;
  grid-row: 1/2;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 125px 275px 60px;
  justify-content: end;
  align-content: center;
}
.header__block p {
  font-family: "Bebas Neue", "Monaco", monospace;
  font-size: 49px;
  font-weight: 400;
  line-height: 59px;
  letter-spacing: 0.03em;
  text-align: end;
  align-self: end;
  grid-column: -1/-2;
  grid-row: 1/2;
  margin: 0 0 10px 0;
}
.header__block h1 {
  font-family: "Bebas Neue", "Monaco", monospace;
  font-size: 109px;
  line-height: 109px;
  letter-spacing: 0.03em;
  text-align: end;
  grid-column: -1/1;
  grid-row: 2/3;
  margin: 0;
}
.header__block img {
  position: relative;
  bottom: 5px;
}
.header__block button {
  width: 230px;
}

.header__socials {
  grid-column: 3/4;
  display: grid;
  gap: 9px;
  list-style-type: none;
  padding: 0;
  margin: 0 0 43px 0;
  justify-self: end;
  align-self: end;
}

.header__links {
  list-style-type: none;
  letter-spacing: -1px;
  padding: 0;
  margin: 40px 0 0 0;
  text-align: end;
  grid-column: 1/2;
  grid-row: 2/3;
  justify-self: end;
  align-self: center;
}

.header__link a {
  color: #ffffff;
}

.main-intro {
  color: #102654;
}

.main-intro__wrapper {
  padding: 120px 103px 236px;
  display: grid;
  grid-template-columns: 355px auto;
  column-gap: 85px;
  justify-content: end;
  position: relative;
}

.main-intro__description h1 {
  margin-top: 50px;
  margin-bottom: 5px;
}
.main-intro__description h2 {
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0em;
  margin-top: 5px;
  margin-bottom: 40px;
}
.main-intro__description p {
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
  letter-spacing: -1px;
  word-spacing: 4px;
  margin-top: 5px;
  margin-bottom: 5px;
}

.main-subscription {
  position: relative;
  color: #102654;
  background-color: #f8f9fc;
}

.main-subscription__wrapper {
  padding: 120px 103px;
}
.main-subscription__wrapper h1 {
  margin: -3px 0 0 0;
  text-align: center;
}

.main-subscription__container {
  display: flex;
  flex-direction: column;
}

.main-subscription__months {
  font-size: 20px;
  font-weight: 400;
  line-height: 30px;
  letter-spacing: 0em;
  text-align: left;
  list-style-type: none;
  display: flex;
  justify-content: center;
  margin: 35px 0 51px;
  padding: 0;
}

.main-subscription__month {
  margin: 0 23px;
}
.main-subscription__month a {
  color: #102654;
}

.main-subscription__tabs {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
}

.main-subscription__tab {
  display: grid;
  aspect-ratio: 360/448;
  max-width: 360px;
  max-height: 448px;
  padding: 56px 72px 77px;
  z-index: 2;
  background-color: #ffffff;
  border: 7px solid transparent;
}
.main-subscription__tab:hover {
  border-image: linear-gradient(to right, #D10A42 0.85%, #00359F 100%);
  -moz-border-image: -moz-linear-gradient(left, #D10A42 0.85%, #00359F 100%);
  -webkit-border-image: -webkit-linear-gradient(left, #D10A42 0.85%, #00359F 100%);
  border-image-slice: 1;
}
.main-subscription__tab:hover > button {
  color: #ffffff;
  outline: none;
  background: linear-gradient(148.82deg, #D10A42 0.85%, #00359F 100%), linear-gradient(0deg, #FFFFFF, #FFFFFF);
}
.main-subscription__tab h2 {
  font-size: 26px;
  font-weight: 700;
  line-height: 29px;
  letter-spacing: 0em;
  text-align: center;
  margin: 0;
}
.main-subscription__tab h3 {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: center;
  margin-top: 10px;
}
.main-subscription__tab p {
  color: #103aae;
  font-size: 60px;
  font-weight: 700;
  line-height: 90px;
  letter-spacing: 0em;
  text-align: center;
  text-shadow: 0 0 0 rgba(0, 0, 0, 0.15), 0 -40px 1px rgba(0, 0, 0, 0.1), 0 0 0 rgba(0, 0, 0, 0.1);
  margin-top: 43px;
  margin-bottom: 36px;
}
.main-subscription__tab span {
  font-family: "Rouble", "Arial", sans-serif;
  color: #102654;
}

.main-subscription__bg {
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 1;
  transform: rotate(0deg);
  transform: translate(15px, 180px);
}

.main-competition {
  color: #ffffff;
}

.main-competition__wrapper {
  padding: 120px 103px 60px;
  display: grid;
  grid-template-columns: 1fr auto;
  justify-content: start;
}

.main-competition__request {
  background-color: #ed0233;
  padding: 85px 79px;
  display: grid;
  column-gap: 48px;
}
.main-competition__request h1 {
  font-family: "Bebas Neue", "Monaco", monospace;
  font-size: 90px;
  line-height: 90px;
  letter-spacing: 0.03em;
  margin: 0;
  grid-column: 1/2;
  grid-row: 1/2;
}
.main-competition__request p {
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0em;
  margin: 0 0 48px 0;
  grid-column: 1/3;
  grid-row: 2/3;
}
.main-competition__request a {
  grid-column: 1/3;
  grid-row: 3/4;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.main-competition__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  grid-column: 2/3;
  grid-row: 1/2;
}

.main-competition__item {
  display: grid;
}
.main-competition__item h2 {
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0em;
  margin: 0;
  padding: 0;
  grid-column: 1/-1;
  grid-row: 1/2;
}
.main-competition__item p {
  font-size: 40px;
  font-weight: 700;
  line-height: 60px;
  letter-spacing: 0em;
  text-align: left;
  margin: 0;
  padding: 0;
  grid-column: 1/2;
  grid-row: 2/3;
}
.main-competition__item span {
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  letter-spacing: 0em;
  margin: 0;
